"use client"

import { useState } from "react"
import { Bell, Search, User, Settings, LogOut, X, Check, Trash2 } from "lucide-react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { ThemeToggle } from "@/components/theme-toggle"
import Link from "next/link"

interface DashboardHeaderProps {
  title: string
  subtitle?: string
}

const initialNotifications = [
  {
    id: 1,
    title: "Stok Oli Menipis",
    message: "Stok Oli Mesin 10W-40 tersisa 10 unit",
    time: "5 menit lalu",
    read: false,
    type: "warning",
  },
  {
    id: 2,
    title: "Unit Selesai",
    message: "Honda Beat - Ahmad Wijaya siap diambil",
    time: "15 menit lalu",
    read: false,
    type: "success",
  },
  {
    id: 3,
    title: "Check-in Baru",
    message: "Yamaha R15 baru saja check-in",
    time: "1 jam lalu",
    read: false,
    type: "info",
  },
  {
    id: 4,
    title: "Pembayaran Diterima",
    message: "Pembayaran dari Citra Dewi telah diterima",
    time: "2 jam lalu",
    read: true,
    type: "success",
  },
  {
    id: 5,
    title: "Stok Busi Menipis",
    message: "Stok Busi NGK tersisa 15 unit",
    time: "3 jam lalu",
    read: true,
    type: "warning",
  },
]

const typeColors = {
  warning: "bg-amber-500",
  success: "bg-emerald-500",
  info: "bg-blue-500",
  error: "bg-red-500",
}

export function DashboardHeader({ title, subtitle }: DashboardHeaderProps) {
  const [notifications, setNotifications] = useState(initialNotifications)
  const [showLogoutDialog, setShowLogoutDialog] = useState(false)

  const unreadCount = notifications.filter((n) => !n.read).length

  const markAsRead = (id: number) => {
    setNotifications(notifications.map((n) => (n.id === id ? { ...n, read: true } : n)))
  }

  const markAllAsRead = () => {
    setNotifications(notifications.map((n) => ({ ...n, read: true })))
  }

  const removeNotification = (id: number) => {
    setNotifications(notifications.filter((n) => n.id !== id))
  }

  const deleteAllNotifications = () => {
    setNotifications([])
  }

  const handleLogout = () => {
    setShowLogoutDialog(false)
    window.location.href = "/"
  }

  return (
    <>
      <header className="flex flex-col gap-4 mb-6 lg:mb-8">
        <div className="flex flex-wrap items-start justify-between gap-3 sm:gap-4">
          <div className="flex-1 min-w-[220px] pl-10 sm:pl-0">
            <h1 className="text-xl lg:text-2xl font-bold text-foreground">{title}</h1>
            {subtitle && <p className="text-muted-foreground text-xs lg:text-sm">{subtitle}</p>}
          </div>

          <div className="flex items-center justify-end flex-wrap gap-2 sm:gap-3 lg:gap-4 flex-shrink-0">
            <div className="relative hidden md:block">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input placeholder="Cari..." className="pl-9 w-48 lg:w-64 bg-secondary border-border" />
            </div>

            <ThemeToggle />

            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" size="icon" className="relative h-8 w-8 sm:h-9 sm:w-9">
                  <Bell className="h-4 w-4 sm:h-5 sm:w-5" />
                  {unreadCount > 0 && (
                    <span className="absolute -top-0.5 -right-0.5 sm:-top-1 sm:-right-1 h-4 w-4 rounded-full bg-destructive text-[10px] font-medium flex items-center justify-center text-destructive-foreground">
                      {unreadCount}
                    </span>
                  )}
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent
                className="w-[340px] p-0 rounded-xl shadow-2xl border-border/50"
                align="end"
                sideOffset={8}
              >
                {/* Header */}
                <div className="p-4 border-b border-border/50 bg-muted/30">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="font-semibold text-sm">Notifikasi</h4>
                      {unreadCount > 0 && (
                        <p className="text-xs text-muted-foreground mt-0.5">{unreadCount} belum dibaca</p>
                      )}
                    </div>
                    <div className="flex items-center gap-1">
                      {unreadCount > 0 && (
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={markAllAsRead}
                          className="text-xs h-8 px-2.5 rounded-lg hover:bg-primary/10 text-primary"
                        >
                          <Check className="h-3.5 w-3.5 mr-1" />
                          Tandai Dibaca
                        </Button>
                      )}
                    </div>
                  </div>
                </div>

                {/* Notification List */}
                <div className="max-h-[320px] overflow-y-auto">
                  {notifications.length === 0 ? (
                    <div className="text-center py-12 px-4">
                      <Bell className="h-10 w-10 mx-auto text-muted-foreground/30 mb-3" />
                      <p className="text-muted-foreground text-sm">Tidak ada notifikasi</p>
                    </div>
                  ) : (
                    <div className="py-1">
                      {notifications.map((notif, index) => (
                        <div
                          key={notif.id}
                          className={`
                            group relative px-4 py-3 cursor-pointer transition-all duration-200
                            hover:bg-muted/50
                            ${!notif.read ? "bg-primary/5" : ""}
                            ${index !== notifications.length - 1 ? "border-b border-border/30" : ""}
                          `}
                          onClick={() => markAsRead(notif.id)}
                        >
                          <div className="flex items-start gap-3">
                            {/* Indicator dot */}
                            <div
                              className={`mt-1.5 h-2 w-2 rounded-full shrink-0 ${typeColors[notif.type as keyof typeof typeColors]}`}
                            />

                            <div className="flex-1 min-w-0">
                              <div className="flex items-center gap-2">
                                <p
                                  className={`text-sm font-medium leading-tight ${!notif.read ? "text-foreground" : "text-muted-foreground"}`}
                                >
                                  {notif.title}
                                </p>
                                {!notif.read && (
                                  <span className="inline-flex h-1.5 w-1.5 rounded-full bg-primary shrink-0" />
                                )}
                              </div>
                              <p className="text-xs text-muted-foreground mt-0.5 line-clamp-2 leading-relaxed">
                                {notif.message}
                              </p>
                              <p className="text-[11px] text-muted-foreground/70 mt-1.5">{notif.time}</p>
                            </div>

                            {/* Delete button */}
                            <Button
                              variant="ghost"
                              size="icon"
                              className="h-7 w-7 opacity-0 group-hover:opacity-100 transition-opacity text-muted-foreground hover:text-destructive hover:bg-destructive/10 rounded-lg shrink-0"
                              onClick={(e) => {
                                e.stopPropagation()
                                removeNotification(notif.id)
                              }}
                            >
                              <X className="h-3.5 w-3.5" />
                            </Button>
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>

                {/* Footer */}
                {notifications.length > 0 && (
                  <div className="p-3 border-t border-border/50 bg-muted/30">
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={deleteAllNotifications}
                      className="w-full text-xs h-8 text-destructive hover:text-destructive hover:bg-destructive/10 rounded-lg"
                    >
                      <Trash2 className="h-3.5 w-3.5 mr-1.5" />
                      Hapus Semua Notifikasi
                    </Button>
                  </div>
                )}
              </DropdownMenuContent>
            </DropdownMenu>

            {/* User dropdown - unchanged */}
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" className="relative h-8 w-8 sm:h-9 sm:w-9 rounded-full p-0">
                  <Avatar className="h-8 w-8 sm:h-9 sm:w-9">
                    <AvatarImage src="/diverse-user-avatars.png" alt="User" />
                    <AvatarFallback className="bg-primary text-primary-foreground text-xs sm:text-sm">
                      AD
                    </AvatarFallback>
                  </Avatar>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent className="w-64" align="end">
                <div className="flex items-center gap-3 p-3">
                  <Avatar className="h-12 w-12">
                    <AvatarImage src="/diverse-user-avatars.png" alt="User" />
                    <AvatarFallback className="bg-primary text-primary-foreground text-lg">AD</AvatarFallback>
                  </Avatar>
                  <div className="flex-1 min-w-0">
                    <p className="font-medium text-sm truncate">Admin Bengkel</p>
                    <p className="text-xs text-muted-foreground truncate">admin@bengkelpro.com</p>
                  </div>
                </div>
                <DropdownMenuSeparator />
                <DropdownMenuItem asChild>
                  <Link href="/profil" className="flex items-center cursor-pointer">
                    <User className="mr-2 h-4 w-4" />
                    Profil Saya
                  </Link>
                </DropdownMenuItem>
                <DropdownMenuItem>
                  <Settings className="mr-2 h-4 w-4" />
                  Pengaturan
                </DropdownMenuItem>
                <DropdownMenuSeparator />
                <DropdownMenuItem
                  className="text-destructive focus:text-destructive cursor-pointer"
                  onSelect={(e) => {
                    e.preventDefault()
                    setShowLogoutDialog(true)
                  }}
                >
                  <LogOut className="mr-2 h-4 w-4" />
                  Keluar
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>
      </header>

      <AlertDialog open={showLogoutDialog} onOpenChange={setShowLogoutDialog}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Konfirmasi Keluar</AlertDialogTitle>
            <AlertDialogDescription>
              Apakah Anda yakin ingin keluar dari akun ini? Anda perlu login kembali untuk mengakses dashboard.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Batal</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleLogout}
              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
            >
              Ya, Keluar
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </>
  )
}
